.p-strong {
 padding: rem(33) 0 rem(28);
 @include mq(tab) {
   padding: rem(48) 0 rem(39);
   
 }

}

.p-strong__title {
 text-align: center;
}

.p-strong__items {
 margin-top: rem(25);
 overflow: hidden;
 @include mq(tab) {
   margin-top: rem(44);
   
 }

}

.p-strong__item{
  @include mq(tab) {
    display: flex;
    justify-content: space-between;
    
  }
}

.p-strong__item + .p-strong__item {
  margin-top: rem(30);

  @include mq(tab) {
    margin-top: rem(54);
    
  }
}


.p-strong__text-box{

  @include mq(tab) {
    flex:  0 1 rem(500);
    margin-right: rem(50);
  }

}

.p-strong__item:nth-child(even) {
  @include mq(tab) {
    flex-direction: row-reverse;
  }
  
  .p-strong__text-box {
    
    @include mq(tab) {
      margin-left: rem(50);
    }
  }

}



.p-strong__category{
display: inline-block;
padding: rem(6) rem(26);
background-color: $ac-color;
color: $white;
font-size: rem(12);
}

.p-strong__item-title{
margin-top: rem(15);
font-size: rem(20);
line-height: (39 / 26);

@include mq(tab) {
  margin-top: rem(29);
  font-size: rem(26);
  
}

}

.p-strong__text{
margin-top: rem(10);
font-size: rem(14);
line-height: (27 / 16);

@include mq(tab) {
  margin-top: rem(28);
  font-size: rem(16);
  
}


}

.p-strong__img {
  width: 100%;
  position: relative;
  margin-top: rem(10);
  opacity: 0;
  @include mq(tab) {
    flex:  0 1 rem(350);
    margin-top: 0;
  }
  
  }
  
  .p-strong__img::before {
    padding-top: 240 / 350 * 100%; //画像縦 / 横
    display: block;
    content: "";
  
  }
  
  .p-strong__img img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    object-fit: cover;
    object-position: center;
    width: 100%;
    height: 100%;
  }

  .p-strong__item:nth-child(1){
    .p-strong__img.is-animated {
    animation-name: slideRight;/*1で解説*/
    animation-fill-mode:forwards;/*開始と終了の状態*/
    animation-duration:.5s;/*アニメーションの長さ*/
    animation-timing-function:linear;/*アニメーションの進行具合*/
    animation-delay: 0.5s;/*アニメーションの開始を遅らせる*/
    animation-direction:normal;/*アニメーションの向き*/
   
    }
  }
  .p-strong__item:nth-child(2){
    .p-strong__img.is-animated {
    animation-name: slideLeft;/*1で解説*/
    animation-fill-mode:forwards;/*開始と終了の状態*/
    animation-duration:.5s;/*アニメーションの長さ*/
    animation-timing-function:linear;/*アニメーションの進行具合*/
    animation-delay: 0.5s;/*アニメーションの開始を遅らせる*/
    animation-direction:normal;/*アニメーションの向き*/
    
    }
  }
  .p-strong__item:nth-child(3){
    .p-strong__img.is-animated {
    animation-name: slideRight;/*1で解説*/
    animation-fill-mode:forwards;/*開始と終了の状態*/
    animation-duration:.5s;/*アニメーションの長さ*/
    animation-timing-function:linear;/*アニメーションの進行具合*/
    animation-delay: 0.5s;/*アニメーションの開始を遅らせる*/
    animation-direction:normal;/*アニメーションの向き*/
    
    }
  }
    

 .p-strong__content {
   margin-top: rem(40);
   background-image: url(../img/price-1.jpg);
   background-repeat: no-repeat;
   background-size: cover;
   background-position: top;
   padding: rem(30) 0 ;
   text-align: center;
   color: $white;
   margin: 0 calc(50% - 50vw);
   width: 100vw;

   @include mq(tab) {
     margin : rem(93) 0 0;
     width: 100%;
     padding: rem(52) 0 rem(53);
     background-size: cover;
    
  }
 }

 .p-strong__content-text {
  font-size: rem(20);
  font-weight: 700;
  
  @include mq(tab) {
    font-size: rem(26);
    
  }
 }

 .p-strong__btn {
  width: 100%;
  max-width: rem(180);
  height: rem(40);
  border: 1px solid $white;
  border-radius: 30px;
  margin: rem(19) auto 0;
  transition:.3s;
  font-size: rem(14);
  animation-name: bururi;/*1で解説*/
  animation-duration:1.0s;/*アニメーションの長さ*/
  animation-iteration-count:infinite;/*動きのループ*/
  
  @include mq(tab) {
    max-width: rem(200);
    height: rem(50);
    font-size: rem(16);
  }

 }

 .p-strong__btn:hover {

  @include mq(tab) {
    background-color: $ac-color;
    border: 1px solid $ac-color;
  }
 }

 .p-strong__btn a {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  
 }